<template>
  <div>
    <el-dialog class="settingForm"
               title=""
               :append-to-body="true"
               :visible.sync="dialogVisible">
      <el-form ref="form"
               :model="form"
               label-width="130px">
        <el-row type="flex"
                class="row-bg">
          <el-col :span="12">
            <el-form-item label="">
              <el-input size="small"
                        v-model="form.title"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="1">
              <el-input size="small"
                        :disabled="form.mask ? true : false"
                        v-model="form.subtitle1"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex"
                class="row-bg">
          <el-col :span="12">
            <el-form-item label="2">
              <el-input size="small"
                        v-model="form.subtitle2"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-select v-model="form.displayMode"
                         size="small"
                         placeholder="">
                <el-option v-for="item in options"
                           :key="item.value"
                           :label="item.label"
                           :value="item.value"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex"
                class="row-bg">
          <el-col :span="12">
            <el-form-item label="">
              <el-radio-group v-model="form.submodule">
                <el-radio label="1"></el-radio>
                <el-radio label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item v-if="form.submodule == '1'"
                          label="">
              <el-radio-group v-model="form.clickToShow">
                <el-radio label="row"></el-radio>
                <el-radio label="cell"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <el-radio-group v-model="form.isPage">
                <el-radio label="1"></el-radio>
                <el-radio label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item v-if="form.isPage == '1'"
                          label="()">
              <el-input-number size="small"
                               v-model="form.pageSize"
                               :min="0"
                               :max="1000"
                               :precision="0"></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex"
                class="row-bg">
          <el-col :span="18">
            <el-form-item label="">
              <el-input size="small"
                        v-model="form.url"
                        placeholder="()"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-button size="small"
                       style="margin-top:2px"
                       @click="getKeysData"></el-button>
          </el-col>
        </el-row>

        <el-form-item label=""
                      :class="['zdpz_box',{'mt':!isWidth}]">
          <p class="tips"
             v-if="!isWidth">*</p>
          <ul class="zdpz_list">
            <li class="zdpz_list_header">
              <span class="hTxt1 hTxt"></span>
              <span class="hTxt2 hTxt"></span>
              <span class="hTxt3 hTxt"
                    v-if="isWidth"></span>
              <span class="hTxt4 hTxt"></span>
              <span class="hTxt7 hTxt"></span>
              <span class="hTxt8 hTxt"></span>
              <!-- <span class="hTxt5 hTxt" v-if="form.clickToShow=='cell'"></span> -->
              <span class="hTxt6 hTxt icons"></span>
            </li>
            <li v-for="(item, index) in form.keyArr"
                :key="index"
                class="zdpz">
              <label class="hTxt1 hTxt">
                <el-input size="mini"
                          placeholder=""
                          v-model="item.key"></el-input>
              </label>
              <label class="hTxt2 hTxt">
                <el-input size="mini"
                          placeholder=""
                          v-model="item.explain"></el-input>
              </label>
              <label class="hTxt3 hTxt"
                     v-if="isWidth">
                <el-input size="mini"
                          placeholder=""
                          v-model="item.width"></el-input>
              </label>
              <label class="hTxt4 hTxt">
                <el-input size="mini"
                          placeholder=""
                          v-model="item.dw"></el-input>
              </label>
              <!-- <label class="hTxt5 hTxt" v-if="form.clickToShow=='cell'">
                <el-input size="mini" placeholder="" v-model="item.relationKey"></el-input>
              </label>-->
              <label class="hTxt7 hTxt">
                <el-checkbox v-model="item.isCruxKey"></el-checkbox>
              </label>
              <label class="hTxt8 hTxt">
                <el-checkbox v-model="item.isShow"></el-checkbox>
              </label>
              <div class="icons hTxt6 hTxt">
                <i @click="keyAdd(index)"
                   class="el-icon-circle-plus-outline  theme-color"></i>
                <i v-show="form.keyArr.length > 1"
                   @click="keyRemove(index)"
                   class="el-icon-remove-outline remove"></i>
                <i @click="sortPrev(item, index, index == 0)"
                   :class="['iconfont', 'iconshangyi', { disabled: index == 0 }]"></i>
                <i @click="
                    sortNext(item, index, form.keyArr.length - 1 == index)
                  "
                   :class="[
                    'iconfont',
                    'iconxiayi',
                    { disabled: form.keyArr.length - 1 == index }
                  ]"></i>
              </div>
            </li>
          </ul>
        </el-form-item>

        <el-row type="flex"
                class="row-bg">
          <el-col :span="12">
            <el-form-item label="()">
              <el-input-number size="small"
                               v-model="form.width"
                               :min="0"
                               :max="100"
                               :precision="2"></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="()">
              <el-input-number size="small"
                               v-model="form.height"
                               :min="0"
                               :max="100"
                               :precision="2"></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex"
                class="row-bg">
          <el-col :span="12">
            <el-form-item label="X()">
              <el-input-number size="small"
                               v-model="form.left"
                               :min="0"
                               :max="100"
                               :precision="2"></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Y()">
              <el-input-number size="small"
                               v-model="form.top"
                               :min="0"
                               :max="100"
                               :precision="2"></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex"
                class="row-bg">
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="form.zindex"
                        size="small"
                        placeholder=","></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="12">
            <el-form-item v-if="form.mask"
                          label="">
              <el-radio-group v-model="form.mask">
                <el-radio label="1"></el-radio>
                <el-radio label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="onSubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import dataPresentation from './dataPresentation.json'
// import axios from "axios";
export default {
  props: ['form'],
  data () {
    return {
      dialogVisible: false,
      options: [],
      defaultData: [
        {
          title: '',
          num: '',
          area: '',
          rowid: '001'
        }
      ]
    }
  },
  mounted () {
    this.options = []
    dataPresentation.forEach(item => {
      this.options.push({
        value: item.type,
        label: item.title
      })
    })
  },
  computed: {
    // 
    isWidth () {
      if (this.form.displayMode === 'list' || this.form.displayMode === 'table') {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    // 
    getKeysData () {
      for (let key in this.defaultData[0]) {
        if (this.form.keyArr.length === 0) {
          this.form.keyArr.push({
            key: key,
            explain: '',
            dw: '',
            width: ''
          })
        } else {
          let offon = true
          this.form.keyArr.forEach(item => {
            if (item.key === key) {
              offon = false
            }
          })
          if (offon) {
            this.form.keyArr.push({
              key: key,
              explain: '',
              dw: '',
              width: ''
            })
          }
        }
      }
    },
    // 
    show () {
      this.dialogVisible = true
    },
    // 
    sortPrev (item, index, offon) {
      if (offon) return
      this.form.keyArr.splice(index, 1)
      this.form.keyArr.splice(index - 1, 0, item)
    },
    // 
    sortNext (item, index, offon) {
      if (offon) return
      this.form.keyArr.splice(index, 1)
      this.form.keyArr.splice(index + 1, 0, item)
    },
    // 
    close () {
      this.dialogVisible = false
    },
    // 
    keyAdd (index) {
      this.form.keyArr.splice(index + 1, 0, { key: '', text: '' })
      //   this.form.keyArr = Object.assign({}, this.form.keyArr);
    },
    // 
    keyRemove (index) {
      this.form.keyArr.splice(index, 1)
    },
    // 
    onSubmit () {
      this.$emit('submit', this.form)
    }
  }
}
</script>



